import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
export default {
	data () {
		return {
			headerTitle: ['全部', '待付款', '拼团中', '拼团成功', '拼团失败'],
			headerIndex: 0,
			groupStatus: ['', '拼团中', '待付款', '已取消','拼团失败', '拼团成功'],
			hasFlag: true,
			pageNum: 0,
			orderList: [],
			status: '',
			swiperFlag: true,
			totalMoney: 0,
			buyOrderId: 0,
			myEduPrice: 0
		}
	},
	components:{
		uniLoadMore
	},
	methods: {
		// 更改TAB
		handleheaderTab (index) {
			console.log(index)
			this.headerIndex = index
			this.pageNum = 0
			this.hasFlag = true
			this.orderList = []
			this.swiperFlag = false // 由于点击和滑动会触发两次  限制 点击不让滑动的执行
			console.log(this.orderList)
			if (index == 0) {
				this.status = ''
			} else if (index == 1) {
				this.status = 2
			}else if (index == 2) {
				this.status = 1
			}else if (index == 3) {
				this.status = 5
			}
			else if (index == 4) {
				this.status = 4
			}
			this.initOrderList()
		},
		// 初始化列表
		async initOrderList () {
			if (!this.hasFlag) return 
			this.pageNum++
			console.log(this.orderList)
			let res = await this.$fetch(this.$api.orderList,{pageNum: this.pageNum, pageSize: 10, status: this.status, isAssemble: 1}, 'GET', 'FORM')
			console.log(res)
		
			this.orderList = [...this.orderList, ...res.rows]
			
			this.swiperFlag = true // 数据请求完 初始化
			this.hasFlag = this.pageNum * 10 < res.total
		},
		// 滑动
		swiperChange (e) {
			// this.headerIndex = e.mp.detail.current
			if (this.swiperFlag) {  // 若是点击 则不执行
				this.handleheaderTab(e.mp.detail.current)
			}
			
		},
		// 触底
		catchBottom () {
			if (this.hasFlag) {
				this.initOrderList()
			}
		},
		goToRecharge () {
			uni.navigateTo({
				url: '../Recharge/Recharge'
			})
		},
		// 取消
		cancelOrder (id) {
			uni.showModal({
			    title: '提示',
			    content: '确定取消该订单么?',
			    success: async (res) =>  {
			        if (res.confirm) {
			            let msg = await this.$fetch(this.$api.cancelOrder,{orderId: id}, 'GET', 'FORM')
			            uni.showToast({
			            	icon: 'none',
							title: msg.msg
			            })
						setTimeout(() => {
							uni.redirectTo({
							   url: './GroupOrder?orderTab=' + 4
							});
						}, 1000)
						
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		// 关闭弹窗
		closePopupPay () {
			 this.$refs.popupPay.close()
		},
		// 支付
		async payOrder () {
			 let msg = await this.$fetch(this.$api.pay,{orderId: this.buyOrderId, pay: true}, 'GET', 'FORM')
			 console.log(msg)
			 uni.showToast({
				icon: 'none',
				title: msg.msg
			 })
			 if (!msg.code) {
				 this.$refs.popupPay.close()
				 setTimeout(() => {
					uni.redirectTo({
						url: './GroupOrder?orderTab=' + 2
					})
				 }, 1000)
			 }
			 
		},
		// 个人信息
		async initUserInfo () {
			let {data: res} = await this.$fetch(this.$api.userInfo,{},'GET', 'form')
			this.myEduPrice = Number(res.money)
			console.log(this.myEduPrice)
		},
		// 显示支付弹框
		showPopWrap (id, money) {
			uni.showModal({
			    title: '提示',
			    content: '确定购买该课程么?',
			    success: async (res) =>  {
			        if (res.confirm) {
						this.totalMoney = money
						this.buyOrderId = id
						this.$refs.popupPay.open()
			
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		// 去课程详情
		handleOpenOrderDetail (id) {
			uni.navigateTo({ url: '../orderDeatail/OrderDetail?orderId=' + id + "&goIndex=" + 2})
		},
	},
	onLoad(options) {
		// 若是操作后的跳转 则默认是点击事件去请求  不是则初始化加载
		if (options.orderTab) {
			this.handleheaderTab(options.orderTab)
		} else {
			this.initOrderList()
		}
	},
	onShow() {
		this.initUserInfo()
		if (uni.getStorageSync("backFlag")) {
			let backIndex = uni.getStorageSync("backFlag")
			uni.removeStorageSync('backFlag')
			this.handleheaderTab(backIndex)
		}
	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.pageNum = 0
			this.hasFlag = true
			this.orderList = []
			this.swiperFlag = false
			this.initOrderList()
			uni.stopPullDownRefresh()
		}, 1000)
	}
}
